.about {
    background-color: #fff5f6;
    width: 100%;
    margin-top: 98px;
  }
  .about__above-title {
    font-family: "Titan One";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    text-transform: uppercase;
    color: #ffa5ba;
    padding-top: 125px;
  }
  .about__title {
    font-family: "Titan One";
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #d41443;
    padding-top: 16px;
    padding-bottom: 50px;
  }
  .about__list {
      position: relative;
      z-index: 1000;
  }
  .about__item {
    display: flex;
    justify-content: center;
  }
  .about__img {
    max-width: 280px;
    width: 100%;
  }
  .about__first-text {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    color: #000000;
    text-align: left;
    margin: 47px auto;
    max-width: 220px;
    max-height: 79px;
  }
  .about__second-text {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.62;
    color: #907e82;
    text-align: left;
    margin: 0 auto;
    max-width: 220px;
    margin-bottom: 47px;
    max-height: 155px;
  }
  .about__button {
    background: #ffffff;
    border-radius: 22px;
    border: none;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    width: 176px;
    height: 44px;
    display: flex;
    align-items: center;
    text-align: left;
    color: #d41443;
    padding-left: 43px;
    margin-right: auto;
    cursor: pointer;
    transition-property: transform;
    transition-duration: 20ms;
    
    transition: background-color 1000ms cubic-bezier(0.165, 0.84, 0.44, 1),
      color 1000ms cubic-bezier(0.165, 0.84, 0.44, 1);
        
      &:hover,
      &:focus {
        transform: scale(1.1);
        background-color: #d41342;
        color: #ffffff;
      }
          
      &:hover .localisation-buttons__icon,
      &:focus .localisation-buttons__icon {
          transform: scale(1.1);
         fill: #ffffff;
      }
  }
  .about__milk {
    width: 100%;
    position:absolute;
    max-height: 133px;
    left: 0;
   top: 3480px;
    z-index: 10;
  }
  .about__milk--desktop {
      display: none;
  }
  @media screen and (min-width: 768px) and (max-width: 1200px) {
    .container {
      padding: 0 36px;
    }
    .about__above-title {
      font-size: 26px;
      line-height: 30px;
      padding-top: 130px;
    }
    .about__title {
      font-size: 48px;
      line-height: 55px;
    }
    .about__milk,
    .about__milk--desktop {
      display: none;
  }
    .about__list {
      display: flex;
      justify-content: center;
    }
    .about__img {
      max-width: 340px;
      max-height: 339px;
      width: 100%;
      margin-right: 17px;
    }
    .about__first-text {
      font-size: 14px;
      line-height: 1.76;
      max-width: 340px;
      margin-top: 50px;
      margin-bottom: 49px;
    }
    .about__second-text {
      font-size: 14px;
      line-height: 1.76;
      max-width: 340px;
      margin-bottom: 62px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .container {
      padding: 0 115px;
      
    }
    .about__above-title {
      font-size: 30px;
      line-height: 34px;
    }
    .about__title {
      font-size: 58px;
      line-height: 66px;
      padding-bottom: 55px;
    }
    .about__milk {
      display: none;
    }
    .about__milk--desktop {
      display: block;
      width: 100%;
      position: absolute;
      left: 0;
      top: 2000px;
      max-height: 534px;
      z-index: 10;
  }
    .about__list {
      display: flex;
      justify-content: center;
    }
    .about__img {
      max-width: 516px;
      max-height: 516px;
      width: 100%;
      margin-right: 74px;
    }
    .about__first-text {
      font-size: 16px;
      line-height: 1.76;
      max-width: 463px;
      margin-top: 50px;
      margin-bottom: 49px;
    }
    .about__second-text {
      font-size: 14px;
      line-height: 1.94;
      max-width: 463px;
      margin-bottom: 45px;
    }
  }
  