.backdrop {
            display: inline-block;
                position: fixed;
                z-index: 2000;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.2);
                transition: 500ms;
                transition-timing-function: ease-in;
}

